.header{
    display: flex;
    position: sticky;
    top: 0;
    background-color: aquamarine;
    height: auto;
    justify-content: space-between;
    padding: 5px 10px;
}

.logo{
    text-decoration: none;
    font-size: 30px;
    font-weight: bold;
}

.header_link{
    display: flex;
}

.header_link ul li{
    list-style: none;
    padding: 5px 20px;
}

.header_link ul li a:hover{
    color: #a3a136;
}
