.img{
    width: 10%;
}

.items{
    display: flex;
    justify-content: space-evenly;
    margin: 10px 10px 10px 10%x;
    padding: 10px 10px;
}

.cart_items{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin: 10px;
    padding: 10px 0px;
}

.btn{
    padding: 5px 10px 7px 10px;
    margin: 5px;
    border-radius: 10px;
    border-style: none;
}

.btn:hover{
    background-color: aqua;
}

.addcart{
    text-align: center;
}

.cart{
    width: 80%;
    text-align: center;
    padding-left: 15%;
}

.totalprice{
    display: flex;
    padding-left: 20%;
    margin: 5px;
}

.removebtn{
    border-style: none;
    background-color: aqua;
    padding: 7px;
    border-radius: 20px;
}

.removebtn:hover{
    background-color: red;
    cursor: pointer;
    color: white;
}

.stock{
    color: red;
}
